import { Plugin } from '@ali/4ever-cangjie';
import { IHoverToolbarPluginConfigs } from './types';

export default function createOnQuery(config: IHoverToolbarPluginConfigs):Plugin['onQuery'] {
  return function onQuery(event, controller, next) {
    const { type, args } = event;
    if (type === 'hasHoverToolbar') {
      const currentType = args[0];
      const { isDisabled, layouts } = config;
      const disabled = isDisabled && isDisabled();
      return !disabled && layouts && !!layouts[currentType];
    }

    return next();
  };
}